import React, { useState, useEffect } from 'react';

const ContactPage = () => {
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        subject: '',
        message: '',
    });

    const [status, setStatus] = useState(''); // 'success', 'error', ''
    const [configData, setConfigData] = useState(null);

    useEffect(() => {
        fetch(`${process.env.PUBLIC_URL}/config.json`)
    .then(response => response.json())
    .then(data => setConfigData(data))
    .catch(error => console.error('Error fetching config:', error));
    }, []);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setStatus('sending');

        if (!configData || !configData.links || !configData.links.contactForm) {
            console.error('Contact form link not available in config.');
            setStatus('error');
            return;
        }

        try {
            const response = await fetch(configData.links.contactForm, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                },
                body: JSON.stringify(formData),
            });

            if (response.ok) {
                setStatus('success');
                setFormData({ name: '', email: '', subject: '', message: '' }); // Clear form
            } else {
                setStatus('error');
            }
        } catch (error) {
            console.error('Error submitting form:', error);
            setStatus('error');
        }
    };

    if (!configData) {
        return <div className="container mt-5 pt-5 text-center">Ładowanie konfiguracji...</div>;
    }

    return (
        <div className="container mt-5 pt-5" style={{ backgroundColor: '#F8F9FA', padding: '20px', borderRadius: '8px' }}>
            <h2 className="text-center mb-4 pb-2 border-bottom border-primary">Skontaktuj się z nami</h2>

            <div className="row justify-content-center">
                <div className="col-md-8">
                    {status === 'success' && (
                        <div className="alert alert-success" role="alert">
                            Twoja wiadomość została wysłana! Skontaktujemy się z Tobą wkrótce.
                        </div>
                    )}
                    {status === 'error' && (
                        <div className="alert alert-danger" role="alert">
                            Wystąpił błąd podczas wysyłania wiadomości. Spróbuj ponownie później.
                        </div>
                    )}
                    {status === 'sending' && (
                        <div className="alert alert-info" role="alert">
                            Wysyłanie wiadomości...
                        </div>
                    )}

                    <form onSubmit={handleSubmit}>
                        {/* Hidden fields for Formspree */}
                        <input type="hidden" name="_replyto" value={formData.email} /> {/* Sender's email for reply */}
                        <input type="hidden" name="_subject" value={`New message from Rex3D website: ${formData.subject}`} />
                        {/* Optional: Redirect to a thank you page after submission */}
                        {/* <input type="hidden" name="_next" value="https://yourdomain.com/thanks.html" /> */}

                        <div className="mb-3">
                            <label htmlFor="name" className="form-label">Imię i Nazwisko</label>
                            <input
                                type="text"
                                className="form-control rounded shadow-sm"
                                id="name"
                                name="name"
                                value={formData.name}
                                onChange={handleChange}
                                required
                            />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="email" className="form-label">Adres E-mail</label>
                            <input
                                type="email"
                                className="form-control rounded shadow-sm"
                                id="email"
                                name="email"
                                value={formData.email}
                                onChange={handleChange}
                                required
                            />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="subject" className="form-label">Temat</label>
                            <input
                                type="text"
                                className="form-control rounded shadow-sm"
                                id="subject"
                                name="subject"
                                value={formData.subject}
                                onChange={handleChange}
                                required
                            />
                        </div>
                        <div className="mb-3">
                            <label htmlFor="message" className="form-label">Wiadomość</label>
                            <textarea
                                className="form-control rounded shadow-sm"
                                id="message"
                                name="message"
                                rows="5"
                                value={formData.message}
                                onChange={handleChange}
                                required
                            ></textarea>
                        </div>
                        <button type="submit" className="btn btn-primary btn-lg custom-btn-primary" disabled={status === 'sending'}>
                            {status === 'sending' ? 'Wysyłanie...' : 'Wyślij Wiadomość'}
                        </button>
                    </form>
                </div>
            </div>
        </div>
    );
};

export default ContactPage;